//
// Fonts
// --------------------------------------------------

@mixin font-face-global-properties() {
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'proximaNovaExtraLight';
  src: url(data:font/opentype;base64,);
  @include font-face-global-properties();
}

@font-face {
  font-family: 'proximaNovaLight';
  src: url(data:font/opentype;base64,);
  @include font-face-global-properties();
}

@font-face {
  font-family: 'proximaNovaRegular';
  src: url(data:font/opentype;base64,);
  @include font-face-global-properties();
}

@font-face {
  font-family: 'proximaNovaRegularItalic';
  src: url(data:font/opentype;base64,);
  @include font-face-global-properties();
}

@font-face {
  font-family: 'proximaNovaBold';
  src: url(data:font/opentype;base64,);
  @include font-face-global-properties();
}

@font-face {
  font-family: 'proximaNovaExtraBold';
  src: url(data:font/opentype;base64,);
  @include font-face-global-properties();
}



